export default function debounce(fn, delay = 500, immediate = true, resultCallback) {
  // 1., 
  let timer = null
  let isInvoke = false

  // 2.
  const _debounce = function(...args) {
    return new Promise((resolve, reject) => {
      // 
      if (timer) clearTimeout(timer)

      // 
      if (immediate && !isInvoke) {
        const result = fn.apply(this, args)
        if (resultCallback) resultCallback(result)
        resolve(result)
        isInvoke = true
      } else {
        // 
        timer = setTimeout(() => {
          // 
          const result = fn.apply(this, args)
          if (resultCallback) resultCallback(result)
          resolve(result)
          isInvoke = false
          timer = null
        }, delay)
      }
    })
  }

  // 
  _debounce.cancel = function() {
    console.log(timer)
    if (timer) clearTimeout(timer)
    timer = null
    isInvoke = false
  }

  return _debounce
}

